<template>
    <div class="search">
      <div class="search-content">
      <b-row>
        <b-col  ></b-col>
      <b-col cols="6" >
        <b-input-group prepend="搜索">
          <b-form-input v-model="keywords" type="text" placeholder="search">
          </b-form-input>
          <b-input-group-append>
            <b-btn variant="outline-success" @click="search('baidu',keywords)">Baidu</b-btn>
            <b-btn variant="outline-success"@click="search('google',keywords)">Google</b-btn>
            <b-btn variant="outline-success"@click="search('youtube',keywords)">Youtube</b-btn>
            <b-btn variant="outline-success"@click="search('bilibili',keywords)">Bilibili</b-btn>
          </b-input-group-append>
        </b-input-group>
      </b-col>
        <b-col ></b-col>
      </b-row>
      </div>
    </div>
</template>

<script>
    export default {
        name: "index",
        data () {
          return {
            keywords : ''
          }
        },
      methods : {
        search(name,keywords){
          console.log(name,keywords)
          switch (name) {
            case 'baidu' :
              window.open(`https://www.baidu.com/s?wd=${keywords}`,'_blank')
              break
            case 'google' :
              window.open(`https://www.google.com/search?q=${keywords}`,'_blank')
                  break
            case 'youtube' :
              window.open(`https://www.youtube.com/results?search_query=${keywords}`,'_blank')
              break
            case 'bilibili' :
              window.open(`https://search.bilibili.com/all?keyword=${keywords}`,'_blank')
              break
          }
        }
      }


    }
</script>

<style scoped type="scss">
.search{
  margin-top: 100px
}
</style>



